<template>
  <v-content>
    <div :style="'margin-top:30vh'"></div>
    <v-layout row justify-center>
      <v-spacer></v-spacer>
      <v-flex>
        <v-card>
          <v-card-text>
            <!--<i class="material-icons display-3" :style="'float: left; margin-top:auto'">{{iconCode}}</i>-->
            <p class="text-xs-center">
              <span class="display-4 accent--text ">
                {{httpCode}}
              </span>
              <v-divider></v-divider><br/>
              <span class="display-1 primary--text">{{message}}</span>
            </p>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-spacer></v-spacer>
    </v-layout>
  </v-content>
</template>
<script>
  export default {
    props: {
      httpCode: {
        type: String,
        default: '000'
      },
      iconCode: {
        type: String,
        default: 'warning'
      },
      message: {
        type: String,
        default: 'No message'
      }
    }
  }
</script>



